@import './global.scss';

.header {
    position: fixed;
    z-index: 90;
    top: 0;
    left: 0;
    width: 100%;
    height: $headerHeight;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    background-color: $dark;

    &.seethrough {
        background-color: transparent;
        h1 {
            opacity: 0;
        }
    }

    &.solid {
        @include shadow;
    }

    h1 {
        @media (max-width: 1000px) {
            font-size: 1.4em;
        }
        transition: 0.1s ease-in;
        color: $light; 
        margin-left: 5vw; 
        font-weight: 200;
    }

    nav {
        @media (max-width: 1000px) {
            display: none;
        }
        a {
            color: $primary;
            text-decoration: none;
            margin:1em;
            transition: 0.1s ease-in;
            cursor: pointer;

            &:hover {
                color: lighten($color: $primary, $amount: 20);
            }
        }
        margin-right: 5vw;
        display: flex;
        justify-content: space-between;
    }
}

.page {
    z-index: 1;
    background-color: $light;
    width: 100%;
    min-height: 120vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-bottom: 10vh;
    overflow: hidden;
}

.footer {
    position: relative;
    background-color: $dark;
    width: 100%;
    height: 50vh;
    @media (max-width: 1000px) {
        height: 500px;
    }
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;

    div.card {
        h3 {
            align-self: center;
            font-weight: 600;
            margin: 0;
            margin-bottom: 1em;
            padding: 0 2em;
            text-align: center;
            border-bottom: 1px solid #{$primary};
        }

        @media (max-width: 1400px) {
            display: none;
        }

        padding: 0.8em 2em;
        align-items: start;
        position: absolute;
        background-color: #{$light};
        top:0;
        right: 0;
        padding-bottom: 2.5em
    }

    form {
        transform: translateY(-15%);
        border-radius: 1.25em;
        padding: 1.5em .5em;
        width: 600px;
        max-width: 90%;
        background-color: #{$primary};
        color: #{$dark};
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;

        @media (max-width: 1000px) {
            height: 500px;
        }

        input[type="submit"] {
            border:none;
            background-color: #{$dark};
            color:#{$light};
            padding: 1em;
            border-radius: 0.4em;
            transition: 0.1s ease-in;
            cursor: pointer;
            &:hover {
                background-color: lighten($color: $dark, $amount: 5);
            }
            &:active {
                outline: none;
            }
        }

        h1 {
            margin: 0;
            font-weight: 400;
            border-bottom: 1px solid #{$dark};
            padding: 0 1em;
        }

        div {
            margin: 0.6em 0;
            h3 {
                margin: 0;
                font-size: 1em;
                font-weight: 400;
            }

            input, textarea {
                width: 100%;
            }

            textarea {
                height: 20vh;
                overflow-y: scroll;
            }

            display: flex;
            flex-direction: column;
            width: 90%;
        }
    }
}