@import "../global.scss";
.skills {
    margin-top: $headerHeight;
    .skillWrapper {
        display: grid;
        @media (min-width: 1000px) {
            grid-template-columns: 1fr 1fr;
            width: 70%;
        }
        
        @media (max-width: 1000px) {
            width: 100%;
            grid-template-columns: 1fr;
        }
        grid-auto-rows:20vh;

        .skill {
            @include shadow;
            animation-fill-mode: backwards !important;
            animation: fadeIn 1s ease-out;
            @for $i from 1 to 20 {
                &:nth-child(#{$i}) {
                    animation-delay: 100ms * $i;
                }
            }

            overflow: hidden;
            margin: auto;
            padding: 0;
            grid-row: span 1;
            grid-column: span 1;
            width: 80%;
            height: 15vh;
            background-color: $dark;
            display: grid;
            grid-template-areas: 
            "title photo"
            "list photo";
            grid-template-rows: 20% 80%;
            grid-template-columns: 1fr 15vh;
            
            h3 {
                text-align: center;
                color: $light;
                grid-area: title;
                width: 90%;
                margin: auto;
                padding: 0.1em 0;
                border-bottom: 1px solid $primary;
            }
            
            div.list {
                display: flex;
                flex-direction: column;
                flex-wrap: wrap;
                grid-area: list;
                width: 100%;
                height: 80%;
                span {
                    margin-left: 5%;
                    font-size: 0.8em;
                    color: $light;
                }
                overflow: hidden;
            }
            
            img {
                margin: auto;
                grid-area: photo;
                max-width: 80%;
                max-height: 80%;
            }
        }
    }
}