@import "../global.scss";

.home {
    .cover {
        * {
            animation: fadeIn 1s ease-in;
        }
        z-index: 20;
        width: 100%;
        height: 50vh;
        background-color: $dark;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        h2 {
            color: $light;
            text-transform: uppercase;
            font-weight: 100;
            font-size: 1.6em;
            border-bottom: 1px solid $primary;
            padding: 0.2em 2em;
        }

        h1 {
            font-weight: 400;
            color: $primary;
            font-size: 2.4em;
        }
    }

    .card {
        position: relative;
        width: 100%;
        max-width: 600px;
        display:flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;

        h3 {
            text-align: center;
            grid-area: title;
            color: $dark;
            padding: 0.2em 2em;
            font-size: 1.2em;
            font-weight: 400;
            border-bottom: 1px solid $primary;
        }

        p {
            line-height: 1.5;
            grid-area: p;
            text-align: center;
        }
    }
}